import noconetnt from '../../components/noconetnt.vue';
export default {
	data() {
		return {
			value:[3,new Date().getMonth()],
			time:{
				month:new Date().getMonth()+1<10?'0'+(new Date().getMonth()+1):new Date().getMonth()+1,
				year:new Date().getFullYear(),
				yearval:[],
				monthval: ['01','02','03','04','05','06','07','08','09','10','11','12'],
			},
			state:{
				focutime:false
			},
			year:new Date().getFullYear(),
			month:new Date().getMonth()+1<10?'0'+(new Date().getMonth()+1):new Date().getMonth()+1,
			date:new Date().getDate(),
			week:['周日','周一','周二','周三','周四','周五','周六'],
			list:[],
			focus:0,
			lists:[],
			leng:''
		};
	},
	onLoad() {
		this.list.length = 42;
		var oDate = new Date();
		for (var i = oDate.getFullYear() - 3; i < oDate.getFullYear() + 4; i++) {
			this.time.yearval.push(i);
		}
	},
	onShow(){
		this.getmonth();
	},
	methods:{
		focu(index){
			this.focus = index;
			this.gettable();
		},
		gettable(){
			var i = this.focus
			uni.request({
				url: this.$api+'/weixin/shishenxin/myscheduleverydaylist',
				data: this.getmd5({
					dateStr:this.list[i].year+'-'+this.list[i].month+'-'+this.list[i].date,
					openId:uni.getStorageSync('openId'),
					// openId:'onR7C05SKrVWwNSZrlQgPbMEL4t4'
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						uni.hideLoading();
						this.lists = []
						var data = res.data.data;
						this.lists = data.infos;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		getmonth(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/myschedulemonthinit',
				data: this.getmd5({
					dateStr:this.year+'-'+this.month,
					openId:uni.getStorageSync('openId'),
					// openId:'onR7C05SKrVWwNSZrlQgPbMEL4t4'
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					this.leng = res.data.data.toString()
					if (res.data.code != 200) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
						});
					} else {
						uni.hideLoading();
						this.list = []
						var data = res.data.data;
						this.list = res.data.data;
						for(var i in data){
							if(data[i].year == this.year){
								if(data[i].month == this.month){
									if(data[i].date == this.date){
										this.focus = i;
										this.gettable();
									}
								}
							}
						}
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		focudata(){
			this.year = this.time.year;
			this.month = this.time.month;
			this.state.focutime = false;
		},
		closesha: function(e) {
			this.state.focutime = false;
		},
		dataChange: function(e) {
			const val = e.detail.value
			this.time.year = this.time.yearval[val[0]];
			this.value = val;
			this.time.month = this.time.monthval[val[1]];
		},
		getMonth(type){
			if(type=='up'){
				if(this.month == '01'){
					this.month = '12'
					this.year--
				}else{
					var month = Number(this.month);
					this.month = --month<10?'0'+month--:month--;
				}
			}else{
				if(this.month == '12'){
					this.month = '01'
					this.year++
				}else{
					var month = Number(this.month);
					this.month = ++month<10?'0'+month++:month++;
				}
			}
			this.getmonth();
		},
		childClick(){
			
		}
	},
	components: {
		noconetnt
	},
}
